import React, { useState, useEffect } from "react";
import Grid from "@material-ui/core/Grid";
import { Input, Button, TextField } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import NumberFormat from "react-number-format";
import { ativos } from "../../data/Ativos";

function Calculator() {
  const [ativo, setAtivo] = useState("");
  const [capital, setCapital] = useState(1000);
  const [risco, setRisco] = useState(100);
  const [stop, setStop] = useState(5);
  const [preco, setPreco] = useState(16.5);
  const [loteCalculado, setLoteCalculado] = useState("");
  const [capitalAlavancado, setCapitalAlavancado] = useState(0);

  // Similar ao componentDidMount e componentDidUpdate:
  useEffect(() => {});

  const handleSelectAtivoChange = (e) => {
    setAtivo(e.target.value);
    const ativoSelecionadoAux = ativos[e.target.value];
    setCapitalAlavancado(ativoSelecionadoAux ? capital * ativoSelecionadoAux.alavancagem * 0.8 : 0);
  };

  const handleCapitalChange = (e) => {
    setCapital(e.target.value);
  };

  const handleRiscoChange = (e) => {
    setRisco(e.target.value);
  };

  const handleStopChange = (e) => {
    setStop(e.target.value);
  };

  const handlePrecoChange = (e) => {
    setPreco(e.target.value);
  };

  const calcularLote = () => {
    const lotePadrao = 100;
    const riscoLotePadrao = lotePadrao * preco * (stop / 100);
    setLoteCalculado((lotePadrao * risco) / riscoLotePadrao);
  };

  return (
    <section>
      <Grid
        container
        direction="row"
        justify="center"
        alignItems="center"
        spacing={1}
      >
        <div>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={1}
          >
            <Grid item>
              <p>Ativo</p>
            </Grid>
            <Grid item>
              <Autocomplete
                options={ativos}
                getOptionLabel={(option) => option.ativo}
                style={{ width: 120 }}
                renderInput={(params) => <TextField {...params}  />}
                onChange={handleSelectAtivoChange}
              />
            </Grid>
          </Grid>
        </div>

        <div>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={1}
          >
            <Grid item>
              <p>Capital</p>
            </Grid>
            <Grid item>
              <Input value={capital} onChange={handleCapitalChange}></Input>
            </Grid>
          </Grid>
        </div>

        <div>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={1}
          >
            <Grid item>
              <p>Risco</p>
            </Grid>
            <Grid item>
              <Input value={risco} onChange={handleRiscoChange}></Input>
            </Grid>
          </Grid>
        </div>

        <div>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={1}
          >
            <Grid item>
              <p>% Stop</p>
            </Grid>
            <Grid item>
              <Input value={stop} onChange={handleStopChange}></Input>
            </Grid>
          </Grid>
        </div>

        <div>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={1}
          >
            <Grid item>
              <p>Preço</p>
            </Grid>
            <Grid item>
              <Input value={preco} onChange={handlePrecoChange}></Input>
            </Grid>
          </Grid>
        </div>

        <Grid item>
          <Button variant="contained" onClick={calcularLote} color="primary">
            Calcular
          </Button>
        </Grid>
      </Grid>

      <p>
        Lote:{" "}
        <NumberFormat
          displayType="text"
          decimalScale={2}
          value={loteCalculado}
        />
      </p>
      <p>
        Capital necessário:{" "}
        <NumberFormat
          displayType="text"
          decimalScale={2}
          prefix="R$ "
          value={loteCalculado * preco}
        />
      </p>
      <p>
        Capital alavancado:{" "}
        <NumberFormat
          displayType="text"
          decimalScale={2}
          prefix="R$ "
          value={capitalAlavancado}
        />
      </p>
    </section>
  );
}

export default Calculator;
