import React from 'react';
import Grid from '@material-ui/core/Grid';
import './App.css';
import Notificator from './components/notificator/Notificator';
import Calculator from './components/calculator/Calculator';

function App() {

  return (
    <div className="App" >
      <header className="App-header">
        <p>
          THelp
        </p>
      </header>
      <Grid style={{padding: 20}} container direction="column" justify="center" alignItems="center" spacing={2} >
          <Grid item >
            <Notificator />
          </Grid>
          <Grid item >
            <Calculator />
          </Grid>
      </Grid>
      
    </div>
  );
}

export default App;
