import React, { useState }  from 'react';
import Grid from '@material-ui/core/Grid';
import { Input, Button, Checkbox } from '@material-ui/core';
import useSound from 'use-sound';
import naosejaotario from '../../assets/naosejaotario.mp3';

function Notificator() {

  const [soundEnabled, setSoundEnabled] = useState(true);
  const [play, { stop }] = useSound(naosejaotario);
  const [timer, setTimer] = useState(0);

  const [timeframe, setTimeframe] = useState('5');
  const [segundos, setSegundos] = useState('1');
  const [datetime, setDatetime] = useState(new Date().toDateString());

  const handleTimeframeChange = e => {
    setTimeframe(e.target.value);
  };

  const handleSegundosChange = e => {
    setSegundos(e.target.value);
  };

  const notificar = () => {
    if(soundEnabled) {
      play();
      setTimeout(() => stop() , 6000);
    }
    
    if (Notification.permission !== 'granted')
        Notification.requestPermission();
      else {
        const d = new Date();
        const hr = new Intl.DateTimeFormat('pt-br', { hour: 'numeric', minute: 'numeric', second: 'numeric' }).format(d);
        setDatetime(hr);

        const notification = new Notification('Fechando barra!', {
          icon: 'clock.png',
          body: hr,
        });
        notification.onclick = function () {
          window.open('http://stackoverflow.com/a/13328397/1269037');
        };

        // play();
      }
  }

  const clickStopNotification = () => {
    clearTimeout(timer);
  };

  const clickStartNotification = () => {
    var date = new Date();
    console.log(date);
    const minutosCheck = [];
    let minutes = date.getMinutes().toString();

    if (timeframe === "60") {
      minutosCheck.push("59");
    } else if (timeframe === "15") {
      minutosCheck.push("14");
      minutosCheck.push("29");
      minutosCheck.push("44");
      minutosCheck.push("59");
    } else if (timeframe === "5") {
      minutosCheck.push("4");
      minutosCheck.push("9");
      minutes = (minutes.length > 1) ? minutes.substring(1) : minutes;
    }



    if ((minutosCheck.indexOf(minutes) >= 0) && date.getSeconds().toString() === segundos) {
      // if(date.getHours() >= 9 && (minutosCheck.indexOf(minutes) >= 0) && date.getSeconds().toString() === segundosParam ) {
      // if (date.getHours() >= 9 && date.getSeconds() === 30) {
      // const som = document.getElementById("som");
      // som.play();
      // showDateTime();
      notificar();
    }

    setTimer(setTimeout(() => clickStartNotification() , 1000));
  }

  const checkEnableSound = (event) => {
    clearTimeout(timer);
    setSoundEnabled(event.target.checked);
  };

  return (
      <section>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
          spacing={2}
        >
          <Grid item >
            <p>Timeframe</p>
          </Grid>
          <Grid item >
            <Input value={timeframe} onChange={handleTimeframeChange} ></Input>
          </Grid>

          <Grid item >
            <p>Segundos</p>
          </Grid>
          <Grid item >
            <Input value={segundos} onChange={handleSegundosChange}></Input>
          </Grid>
        </Grid>
        
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
          spacing={2}
        >
          <Grid item >
            <Button variant="contained" onClick={clickStartNotification} color="primary">Start</Button>
          </Grid>
          <Grid item >
            <Button variant="contained" onClick={clickStopNotification} color="secondary">Stop</Button>
          </Grid>
          <Grid item >
            <Button variant="contained" onClick={notificar}>Teste</Button>
          </Grid>

          <Checkbox
            name="sound-enabled"
            checked={soundEnabled}
            size="medium"
            label="Habilitar som"
            onChange={checkEnableSound}
          /> Som
        </Grid>

        <p>Última notificação: {datetime}</p>
      </section>
  );
}

export default Notificator;
